.wrapper {
  min-width: 556px;
  position: relative;
  display: flex;
  flex-direction: column;
  min-height: calc(100 * var(--vh, 1vh));
}
.wrapper .header,
.wrapper .footer {
  flex-shrink: 0;
}
.wrapper main {
  flex-grow: 1;
}

.visually-hidden {
  position: absolute;
  width: 1px;
  height: 1px;
  margin: -1px;
  padding: 0;
  overflow: hidden;
  white-space: nowrap;
  border: 0;
  clip: rect(0 0 0 0);
  clip-path: inset(100%);
}

.list {
  list-style-type: none;
  padding: 0;
  margin: 0;
}

.container {
  box-sizing: border-box;
  width: 1280px;
  margin: 0 auto;
  padding: 80px;
}

.main-index {
  width: 100%;
  background-color: #f1fbf9;
  display: flex;
  align-items: center;
  justify-content: center;
}

.sapper {
  margin: auto;
}

.sapper__container {
  background-color: #c0c0c0;
  width: 556px;
  height: 640px;
  padding: 10px;
  border: solid 4px #ffffff;
  border-right: solid 4px #7b7b7b;
  border-bottom: solid 4px #7b7b7b;
  display: flex;
  flex-wrap: wrap;
  flex-direction: column;
  justify-content: space-between;
  align-items: center;
}

.sapper__info {
  padding: 8px 12px;
  display: flex;
  justify-content: space-between;
  align-items: center;
  box-sizing: border-box;
  width: 100%;
  height: 74px;
  border: solid 4px #ffffff;
  border-left: solid 4px #7b7b7b;
  border-top: solid 4px #7b7b7b;
}

.sapper__field {
  box-sizing: border-box;
  width: 520px;
  height: 520px;
  border: solid 4px #ffffff;
  border-left: solid 4px #7b7b7b;
  border-top: solid 4px #7b7b7b;
}

.sapper__play {
  cursor: pointer;
  border: none;
  background-color: #ffffff;
  padding: 0;
  width: 50px;
  height: 50px;
  background-image: url("../img/sprite@2.png");
  background-repeat: no-repeat;
